@import "https://api.tiles.mapbox.com/mapbox-gl-js/v0.46.0/mapbox-gl.css";.Places{border-bottom:1px dashed grey;z-index:1;top:0;left:0}.Places .map-place,.Places .mapboxgl-canvas{z-index:0}.Loading{margin:0 auto;text-align:center}.Loading .line{display:inline-block;width:12px;height:12px;border-radius:12px;background:#fff}.Loading .line:nth-last-child(1){-webkit-animation:loading .6s .1s linear infinite;animation:loading .6s .1s linear infinite}.Loading .line:nth-last-child(2){-webkit-animation:loading .6s .2s linear infinite;animation:loading .6s .2s linear infinite}.Loading .line:nth-last-child(3){-webkit-animation:loading .6s .3s linear infinite;animation:loading .6s .3s linear infinite}.Loading .line:nth-last-child(4){-webkit-animation:loading .6s .4s linear infinite;animation:loading .6s .4s linear infinite}@-webkit-keyframes loading{0%, 100%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}50%{-webkit-transform:translate(0, 15px);transform:translate(0, 15px)}}@keyframes loading{0%, 100%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}50%{-webkit-transform:translate(0, 15px);transform:translate(0, 15px)}}body{background:#21242E;color:#fff}.App .btn-access-location{display:block;margin:20px auto}



